@import "../../components/globalcss/color.scss";

.home{
    display: flex;
    flex-flow: row wrap;
    justify-content: space-evenly;
    align-items: flex-start;
    background-color: $yellow;
    @mixin myPic {
        height: 15rem;
        width: 25vw;
        background-size: 50rem;
    }
    .warrior{
        background: url("./image/warrior.jpg") no-repeat;
        // 百分比计算公式为 (container width - image width) * (position x%), 为负值-图片左移,即展示到图片 60% 处
        background-position: 60% top; 
        @include myPic();
    }
    .mage{
        background: url("./image/mage.jpg") no-repeat;
        background-position: 61% top; 
        @include myPic();
    }
    .archer{
        background: url("./image/archer.jpg") no-repeat;
        background-position: 62% top; 
        @include myPic();
    }
    .thief{
        background: url("./image/thief.jpg") no-repeat;
        background-position: 62% top; 
        @include myPic();
    }
    .pirate{
        background: url("./image/pirate.jpg") no-repeat;
        background-position: 62% top; 
        @include myPic();
    }
    .aran{
        background: url("./image/aran.jpg") no-repeat;
        background-position: 60% top; 
        @include myPic();
    }
}